"use client";

import React, { useState } from "react";

interface Category {
  id: string;
  name: string;
  description: string;
  image: File | null;
  imagePreview: string | null;
}

export default function CategoryForm() {
  const [categories, setCategories] = useState<Category[]>([]);
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [image, setImage] = useState<File | null>(null);
  const [imagePreview, setImagePreview] = useState<string | null>(null);

  function handleImageChange(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0] ?? null;
    setImage(file);
    if (file) {
      setImagePreview(URL.createObjectURL(file));
    } else {
      setImagePreview(null);
    }
  }

  function handleAddCategory() {
    if (!name.trim()) return;
    const newCategory: Category = {
      id: Date.now().toString(),
      name: name.trim(),
      description: description.trim(),
      image,
      imagePreview,
    };
    setCategories((prev) => [...prev, newCategory]);
    setName("");
    setDescription("");
    setImage(null);
    setImagePreview(null);
  }

  function handleDeleteCategory(id: string) {
    setCategories((prev) => prev.filter((cat) => cat.id !== id));
  }

  return (
    <div>
      <div className="mb-6 space-y-4 max-w-md">
        <input
          type="text"
          placeholder="Nombre de la categoría"
          value={name}
          onChange={(e) => setName(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2"
        />
        <textarea
          placeholder="Descripción"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2 resize-none"
          rows={3}
        />
        <input type="file" accept="image/*" onChange={handleImageChange} />
        {imagePreview && (
          <img
            src={imagePreview}
            alt="Preview Categoría"
            className="mt-2 w-32 h-32 object-cover rounded border border-gray-300"
          />
        )}
        <button
          onClick={handleAddCategory}
          className="bg-black text-white px-4 py-2 rounded hover:bg-gray-800 transition"
        >
          Agregar Categoría
        </button>
      </div>
      <div>
        <h3 className="text-xl font-semibold mb-3">Categorías Agregadas</h3>
        {categories.length === 0 && <p>No hay categorías agregadas.</p>}
        <ul className="space-y-4">
          {categories.map((cat) => (
            <li
              key={cat.id}
              className="flex items-center space-x-4 border border-gray-300 rounded p-3"
            >
              {cat.imagePreview && (
                <img
                  src={cat.imagePreview}
                  alt={cat.name}
                  className="w-16 h-16 object-cover rounded"
                />
              )}
              <div className="flex-1">
                <p className="font-semibold">{cat.name}</p>
                <p className="text-sm text-gray-600">{cat.description}</p>
              </div>
              <button
                onClick={() => handleDeleteCategory(cat.id)}
                className="text-red-600 hover:underline"
              >
                Eliminar
              </button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
