"use client";

import React, { useState } from "react";

export default function CommerceProfile() {
  const [profileImage, setProfileImage] = useState<File | null>(null);
  const [bannerImage, setBannerImage] = useState<File | null>(null);
  const [profilePreview, setProfilePreview] = useState<string | null>(null);
  const [bannerPreview, setBannerPreview] = useState<string | null>(null);

  function handleProfileChange(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0] ?? null;
    setProfileImage(file);
    if (file) {
      setProfilePreview(URL.createObjectURL(file));
    } else {
      setProfilePreview(null);
    }
  }

  function handleBannerChange(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0] ?? null;
    setBannerImage(file);
    if (file) {
      setBannerPreview(URL.createObjectURL(file));
    } else {
      setBannerPreview(null);
    }
  }

  return (
    <div className="space-y-6">
      <div>
        <label className="block font-semibold mb-2">Imagen de Perfil del Comercio</label>
        <input type="file" accept="image/*" onChange={handleProfileChange} />
        {profilePreview && (
          <img
            src={profilePreview}
            alt="Preview Perfil"
            className="mt-2 w-32 h-32 object-cover rounded-full border border-gray-300"
          />
        )}
      </div>
      <div>
        <label className="block font-semibold mb-2">Imagen Banner del Comercio</label>
        <input type="file" accept="image/*" onChange={handleBannerChange} />
        {bannerPreview && (
          <img
            src={bannerPreview}
            alt="Preview Banner"
            className="mt-2 w-full max-w-md h-40 object-cover rounded border border-gray-300"
          />
        )}
      </div>
    </div>
  );
}
