"use client";

import React, { useState } from "react";

interface Product {
  id: string;
  name: string;
  description: string;
  category: string;
  size: string;
  price: number;
  image: File | null;
  imagePreview: string | null;
}

export default function ProductForm() {
  const [products, setProducts] = useState<Product[]>([]);
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [category, setCategory] = useState("");
  const [size, setSize] = useState("");
  const [price, setPrice] = useState("");
  const [image, setImage] = useState<File | null>(null);
  const [imagePreview, setImagePreview] = useState<string | null>(null);

  function handleImageChange(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0] ?? null;
    setImage(file);
    if (file) {
      setImagePreview(URL.createObjectURL(file));
    } else {
      setImagePreview(null);
    }
  }

  function handleAddProduct() {
    if (!name.trim() || !category.trim() || !price.trim()) return;
    const newProduct: Product = {
      id: Date.now().toString(),
      name: name.trim(),
      description: description.trim(),
      category: category.trim(),
      size: size.trim(),
      price: parseFloat(price),
      image,
      imagePreview,
    };
    setProducts((prev) => [...prev, newProduct]);
    setName("");
    setDescription("");
    setCategory("");
    setSize("");
    setPrice("");
    setImage(null);
    setImagePreview(null);
  }

  function handleDeleteProduct(id: string) {
    setProducts((prev) => prev.filter((prod) => prod.id !== id));
  }

  return (
    <div>
      <div className="mb-6 space-y-4 max-w-md">
        <input
          type="text"
          placeholder="Nombre del producto"
          value={name}
          onChange={(e) => setName(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2"
        />
        <textarea
          placeholder="Descripción"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2 resize-none"
          rows={3}
        />
        <input
          type="text"
          placeholder="Categoría"
          value={category}
          onChange={(e) => setCategory(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2"
        />
        <input
          type="text"
          placeholder="Tamaño"
          value={size}
          onChange={(e) => setSize(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2"
        />
        <input
          type="number"
          placeholder="Precio"
          value={price}
          onChange={(e) => setPrice(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2"
          min="0"
          step="0.01"
        />
        <input type="file" accept="image/*" onChange={handleImageChange} />
        {imagePreview && (
          <img
            src={imagePreview}
            alt="Preview Producto"
            className="mt-2 w-32 h-32 object-cover rounded border border-gray-300"
          />
        )}
        <button
          onClick={handleAddProduct}
          className="bg-black text-white px-4 py-2 rounded hover:bg-gray-800 transition"
        >
          Agregar Producto
        </button>
      </div>
      <div>
        <h3 className="text-xl font-semibold mb-3">Productos Agregados</h3>
        {products.length === 0 && <p>No hay productos agregados.</p>}
        <ul className="space-y-4">
          {products.map((prod) => (
            <li
              key={prod.id}
              className="flex items-center space-x-4 border border-gray-300 rounded p-3"
            >
              {prod.imagePreview && (
                <img
                  src={prod.imagePreview}
                  alt={prod.name}
                  className="w-16 h-16 object-cover rounded"
                />
              )}
              <div className="flex-1">
                <p className="font-semibold">{prod.name}</p>
                <p className="text-sm text-gray-600">{prod.description}</p>
                <p className="text-sm">
                  Categoría: {prod.category} | Tamaño: {prod.size}
                </p>
                <p className="text-sm font-semibold">${prod.price.toFixed(2)}</p>
              </div>
              <button
                onClick={() => handleDeleteProduct(prod.id)}
                className="text-red-600 hover:underline"
              >
                Eliminar
              </button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
